html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :before, :after {
  box-sizing: inherit;
}

body, html {
  font: 16px / 1.4 Georgia, serif;
}

.page {
  min-width: 1000px;
  padding: 2.5rem 2rem;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.title {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: .05em;
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: 700;
}

.gallery {
  margin: 0;
  padding: 0;
  font-size: 0;
}

.galleryItem {
  width: 25%;
  margin: 0;
  padding: 0;
  display: inline-block;
}

.card {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
}

.card:hover .cardImage {
  transform: scale(1.2);
}

.card:hover .cardContent {
  opacity: 1;
}

.cardLink {
  margin: 0;
  padding: 0;
  display: block;
}

.cardContent {
  height: 100%;
  width: 100%;
  opacity: 0;
  color: #fff;
  z-index: 1;
  background-color: rgba(0, 0, 0, .7);
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  padding: 40px;
  font-size: 10px;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.cardTitle {
  text-transform: uppercase;
  color: #daa35b;
  text-align: center;
  margin: 0;
  font-size: 20px;
}

.cardImage {
  width: 100%;
  margin: 0;
  padding: 0;
  transition: transform 3s;
  display: block;
  transform: scale(1);
}

